/* Main Container */
.register-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
  font-family: 'Arial', sans-serif;
}

/* Header Section */
.register-header {
  background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
  padding: 15px 20px;
  border-bottom: 2px solid #8B1C1C;
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
}

.logo-section {
  display: flex;
  align-items: center;
}

.logo-image {
  width: clamp(120px, 15vw, 200px);
  height: clamp(120px, 15vw, 200px);
  object-fit: contain;
  border: none !important;
  background: none !important;
  border-radius: 0 !important;
  box-shadow: none !important;
}

.logo-section {
  border: none !important;
  background: none !important;
  border-radius: 0 !important;
  box-shadow: none !important;
}

/* Admin Bar */
.admin-bar {
  background: #8B1C1C;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.admin-button {
  background: none;
  color: white;
  border: none;
  padding: 6px 15px;
  font-size: 1.5rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
  border-radius: 6px;
}

.admin-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

/* Main Content */
.register-main {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 20px;
}

.register-form-container {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 12px;
  padding: 30px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.15);
  max-width: 500px;
  width: 100%;
}

.register-title {
  font-size: 2.2rem;
  font-weight: bold;
  color: #8B1C1C;
  text-align: center;
  margin-bottom: 25px;
  text-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.register-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.form-label {
  font-size: 1rem;
  font-weight: 600;
  color: #333;
}

.form-input {
  padding: 12px;
  border: 2px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
  background: white;
}

.form-input:focus {
  outline: none;
  border-color: #8B1C1C;
  box-shadow: 0 0 0 3px rgba(139, 28, 28, 0.1);
}

.form-select {
  padding: 12px;
  border: 2px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
  background: white;
  cursor: pointer;
}

.form-select:focus {
  outline: none;
  border-color: #8B1C1C;
  box-shadow: 0 0 0 3px rgba(139, 28, 28, 0.1);
}

.submit-button {
  background: #8B1C1C;
  color: white;
  border: none;
  padding: 12px 20px;
  font-size: 1.1rem;
  font-weight: bold;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 15px;
}

.submit-button:hover {
  background: #6B0F0F;
  transform: translateY(-1px);
}

.submit-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.success-message {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 12px 15px;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: bold;
  background: rgba(40, 167, 69, 0.1);
  color: #28a745;
  border: 2px solid #28a745;
  margin-top: 15px;
}

.success-icon {
  font-size: 1.3rem;
}

/* Footer */
.register-footer {
  background: #f9b233;
  padding: 16px 24px;
  margin-top: auto;
  flex-shrink: 0;
}

.footer-content {
  display: flex !important;
  flex-direction: row !important;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  font-size: 1.9rem;
  color: #000;
  font-weight: bold;
}

.footer-section {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1;
}

.footer-text {
  font-size: 1.9rem;
  color: #000;
  font-weight: bold;
  line-height: 1.2;
}

.footer-text:has(div) {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.powered-by {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 4px;
}

.powered-text {
  font-size: 1.8rem;
  color: #000;
  font-weight: bold;
  margin-right: 8px;
  display: inline-block;
}

.pits-logo {
  height: 12rem;
  width: 20rem;
  object-fit: contain;
}

/* Responsive Design for iPad Pro (1024x1336) */
@media screen and (max-width: 1024px) {
  .header-content {
    flex-direction: column;
    gap: 15px;
  }
  
  .org-info {
    margin-left: 0;
    text-align: center;
    font-size: 0.8rem;
  }
  
  .register-title {
    font-size: 2rem;
  }
  
  .register-form-container {
    padding: 25px;
    max-width: 450px;
  }
  
  .footer-content {
    flex-direction: column;
    gap: 12px;
  }
  
  .footer-section {
    justify-content: center;
    min-width: auto;
  }
}

/* Responsive Design for smaller tablets */
@media screen and (max-width: 768px) {
  .register-header {
    padding: 12px 15px;
  }
  
  .register-main {
    padding: 20px 15px;
  }
  
  .register-title {
    font-size: 1.8rem;
    margin-bottom: 20px;
  }
  
  .register-form-container {
    padding: 20px;
    margin: 0 15px;
  }
  
  .form-label {
    font-size: 0.9rem;
  }
  
  .form-input,
  .form-select {
    padding: 10px;
    font-size: 0.9rem;
  }
  
  .form-actions {
    flex-direction: column;
    gap: 12px;
  }
  
  .footer-content {
    padding: 0 8px;
  }
  
  .footer-text {
    font-size: 0.8rem;
  }
}

/* Responsive Design for mobile */
@media screen and (max-width: 480px) {
  .register-header {
    padding: 10px 12px;
  }
  
  .circular-logo {
    width: 70px;
    height: 70px;
  }
  
  .logo-image {
    width: 50px;
    height: 50px;
  }
  
  .logo-text {
    font-size: 0.7rem;
    bottom: -18px;
  }
  
  .org-info {
    font-size: 0.7rem;
  }
  
  .register-title {
    font-size: 1.6rem;
    margin-bottom: 15px;
  }
  
  .register-form-container {
    padding: 15px;
    margin: 0 8px;
  }
  
  .form-label {
    font-size: 0.8rem;
  }
  
  .form-input,
  .form-select {
    padding: 8px;
    font-size: 0.8rem;
  }
  
  .cancel-button,
  .submit-button {
    font-size: 1.1rem;
    padding: 10px 18px;
  }
  
  .footer-content {
    gap: 8px;
  }
  
  .footer-text {
    font-size: 0.7rem;
  }
  
  .pits-logo {
    height: 25px;
  }
} 