/* Main Container */
.checkin-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
  font-family: 'Arial', sans-serif;
}

/* Header Section */
.checkin-header {
  background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
  padding: 15px 20px;
  border-bottom: 2px solid #8B1C1C;
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
}

.logo-section {
  display: flex;
  align-items: center;
}

.logo-image {
  width: clamp(120px, 15vw, 200px);
  height: clamp(120px, 15vw, 200px);
  object-fit: contain;
  border: none !important;
  background: none !important;
  border-radius: 0 !important;
  box-shadow: none !important;
}

.logo-section {
  border: none !important;
  background: none !important;
  border-radius: 0 !important;
  box-shadow: none !important;
}

/* Admin Bar */
.admin-bar {
  background: #8B1C1C;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.admin-button {
  background: none;
  color: white;
  border: none;
  padding: 6px 15px;
  font-size: 1.5rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
  border-radius: 6px;
}

.admin-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

/* Main Content */
.checkin-main {
  flex: 1;
  padding: 30px 20px;
}

.checkin-content {
  max-width: 800px;
  margin: 0 auto;
}

.checkin-title {
  font-size: 2.2rem;
  font-weight: bold;
  color: #8B1C1C;
  text-align: center;
  margin-bottom: 30px;
  text-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

/* Event/Newsletter Section */
.event-newsletter-section {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 12px;
  padding: 25px;
  margin-bottom: 25px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.15);
}

.section-title {
  font-size: 1.8rem;
  font-weight: bold;
  color: #8B1C1C;
  margin-bottom: 15px;
  border-bottom: 2px solid #8B1C1C;
  padding-bottom: 8px;
}

.event-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.event-item {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 15px;
  border: 2px solid #e9ecef;
  cursor: pointer;
  transition: all 0.3s ease;
}

.event-item:hover {
  background: #e9ecef;
  transform: translateY(-1px);
  box-shadow: 0 3px 10px rgba(0,0,0,0.1);
}

.event-item.selected {
  background: #8B1C1C;
  color: white;
  border-color: #6B0F0F;
}

.event-name {
  font-size: 1.1rem;
  font-weight: bold;
  margin-bottom: 6px;
}

.event-details {
  font-size: 0.9rem;
  color: #666;
}

.event-item.selected .event-details {
  color: #f0f0f0;
}

/* Scanner Section */
.scanner-section {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 12px;
  padding: 25px;
  margin-bottom: 25px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.15);
  text-align: center;
}

.scanner-container {
  max-width: 450px;
  margin: 0 auto;
}

.scanner-title {
  font-size: 1.6rem;
  font-weight: bold;
  color: #8B1C1C;
  margin-bottom: 15px;
}

.scanner-instructions {
  font-size: 1rem;
  color: #666;
  margin-bottom: 20px;
  line-height: 1.4;
}

.scanner-element {
  border: 2px solid #8B1C1C;
  border-radius: 10px;
  overflow: hidden;
  margin: 0 auto;
  max-width: 350px;
}

/* Status Messages */
.status-message {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 12px 15px;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: bold;
  margin: 15px 0;
}

.success-message {
  background: rgba(40, 167, 69, 0.1);
  color: #28a745;
  border: 2px solid #28a745;
}

.error-message {
  background: rgba(220, 53, 69, 0.1);
  color: #dc3545;
  border: 2px solid #dc3545;
}

.success-icon,
.error-icon {
  font-size: 1.3rem;
}

/* Footer */
.checkin-footer {
  background: #f9b233;
  padding: 16px 24px;
  margin-top: auto;
  flex-shrink: 0;
}

.footer-content {
  display: flex !important;
  flex-direction: row !important;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  font-size: 1.9rem;
  color: #000;
  font-weight: bold;
}

.footer-section {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1;
}

.footer-text {
  font-size: 1.9rem;
  color: #000;
  font-weight: bold;
  line-height: 1.2;
}

.footer-text:has(div) {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.powered-by {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 4px;
}

.powered-text {
  font-size: 1.8rem;
  color: #000;
  font-weight: bold;
  margin-right: 8px;
  display: inline-block;
}

.pits-logo {
  height: 12rem;
  width: 20rem;
  object-fit: contain;
}

/* Responsive Design for iPad Pro (1024x1336) */
@media screen and (max-width: 1024px) {
  .header-content {
    flex-direction: column;
    gap: 15px;
  }
  
  .org-info {
    margin-left: 0;
    text-align: center;
    font-size: 0.8rem;
  }
  
  .checkin-title {
    font-size: 2rem;
  }
  
  .section-title {
    font-size: 1.6rem;
  }
  
  .event-newsletter-section,
  .scanner-section {
    padding: 20px;
  }
  
  .footer-content {
    flex-direction: column;
    gap: 12px;
  }
  
  .footer-section {
    justify-content: center;
    min-width: auto;
  }
}

/* Responsive Design for smaller tablets */
@media screen and (max-width: 768px) {
  .checkin-header {
    padding: 12px 15px;
  }
  
  .checkin-main {
    padding: 20px 15px;
  }
  
  .checkin-title {
    font-size: 1.8rem;
    margin-bottom: 25px;
  }
  
  .section-title {
    font-size: 1.4rem;
  }
  
  .event-newsletter-section,
  .scanner-section {
    padding: 20px;
    margin-bottom: 20px;
  }
  
  .event-name {
    font-size: 1rem;
  }
  
  .event-details {
    font-size: 0.8rem;
  }
  
  .scanner-title {
    font-size: 1.4rem;
  }
  
  .scanner-instructions {
    font-size: 0.9rem;
  }
  
  .footer-content {
    padding: 0 8px;
  }
  
  .footer-text {
    font-size: 0.8rem;
  }
}

/* Responsive Design for mobile */
@media screen and (max-width: 480px) {
  .checkin-header {
    padding: 10px 12px;
  }
  
  .circular-logo {
    width: 70px;
    height: 70px;
  }
  
  .logo-image {
    width: 50px;
    height: 50px;
  }
  
  .logo-text {
    font-size: 0.7rem;
    bottom: -18px;
  }
  
  .org-info {
    font-size: 0.7rem;
  }
  
  .checkin-title {
    font-size: 1.6rem;
    margin-bottom: 20px;
  }
  
  .section-title {
    font-size: 1.2rem;
  }
  
  .event-newsletter-section,
  .scanner-section {
    padding: 12px;
    margin-bottom: 12px;
  }
  
  .event-item {
    padding: 12px;
  }
  
  .event-name {
    font-size: 0.9rem;
  }
  
  .event-details {
    font-size: 0.7rem;
  }
  
  .scanner-title {
    font-size: 1.2rem;
  }
  
  .scanner-instructions {
    font-size: 0.8rem;
  }
  
  .status-message {
    font-size: 0.9rem;
    padding: 10px 12px;
  }
  
  .footer-content {
    gap: 8px;
  }
  
  .footer-text {
    font-size: 0.7rem;
  }
  
  .pits-logo {
    height: 25px;
  }
}