/* Main Container */
.home-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
  font-family: 'Arial', sans-serif;
}

/* Header Section */
.home-header {
  background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
  padding: 15px 20px;
  border-bottom: 2px solid #8B1C1C;
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
}

.logo-section {
  display: flex;
  align-items: center;
}

.logo-image {
  width: clamp(120px, 15vw, 200px);
  height: clamp(120px, 15vw, 200px);
  object-fit: contain;
  border: none !important;
  background: none !important;
  border-radius: 0 !important;
  box-shadow: none !important;
}

.logo-section {
  border: none !important;
  background: none !important;
  border-radius: 0 !important;
  box-shadow: none !important;
}

/* Admin Bar */
.admin-bar {
  background: #8B1C1C;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.admin-button {
  background: none;
  color: white;
  border: none;
  padding: 6px 15px;
  font-size: 1.5rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
  border-radius: 6px;
}

.admin-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

/* Main Content */
.home-main {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 30px 20px;
  text-align: center;
}

.welcome-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #8B1C1C;
  margin-bottom: 30px;
  text-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.main-image-container {
  margin: 30px 0;
  max-width: 500px;
  width: 100%;
}

.main-image {
  width: 100%;
  height: auto;
  border-radius: 12px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.2);
  border: 2px solid #8B1C1C;
}

.checkin-button {
  background: linear-gradient(135deg, #8B1C1C 0%, #6B0F0F 100%);
  color: white;
  border: none;
  padding: 15px 30px;
  font-size: 1.6rem;
  font-weight: bold;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(139, 28, 28, 0.3);
  margin-top: 25px;
}

.checkin-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(139, 28, 28, 0.4);
  background: linear-gradient(135deg, #6B0F0F 0%, #4A0A0A 100%);
}

/* Footer */
.home-footer {
  background: #f9b233;
  padding: 16px 24px;
  margin-top: auto;
  flex-shrink: 0;
}

.footer-content {
  display: flex !important;
  flex-direction: row !important;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  font-size: 1.9rem;
  color: #000;
  font-weight: bold;
}

.footer-section {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1;
}

.footer-text {
  font-size: 1.9rem;
  color: #000;
  font-weight: bold;
  line-height: 1.2;
}

.footer-text:has(div) {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.powered-by {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 4px;
}

.powered-text {
  font-size: 1.8rem;
  color: #000;
  font-weight: bold;
  margin-right: 8px;
  display: inline-block;
}

.pits-logo {
  height: 12rem;
  width: 20rem;
  object-fit: contain;
}

/* Responsive Design for iPad Pro (1024x1336) */
@media screen and (max-width: 1024px) {
  .header-content {
    flex-direction: column;
    gap: 15px;
  }
  
  .org-info {
    margin-left: 0;
    text-align: center;
    font-size: 0.8rem;
  }
  
  .welcome-title {
    font-size: 2.2rem;
  }
  
  .main-image-container {
    max-width: 450px;
  }
  
  .checkin-button {
    font-size: 1.4rem;
    padding: 12px 25px;
  }
  
  .footer-content {
    flex-direction: column;
    gap: 12px;
  }
  
  .footer-section {
    justify-content: center;
    min-width: auto;
  }
}

/* Responsive Design for smaller tablets */
@media screen and (max-width: 768px) {
  .home-header {
    padding: 12px 15px;
  }
  
  .home-main {
    padding: 25px 15px;
  }
  
  .welcome-title {
    font-size: 2rem;
    margin-bottom: 25px;
  }
  
  .main-image-container {
    margin: 25px 0;
  }
  
  .checkin-button {
    font-size: 1.3rem;
    padding: 10px 20px;
  }
  
  .footer-content {
    padding: 0 8px;
  }
  
  .footer-text {
    font-size: 0.8rem;
  }
}

/* Responsive Design for mobile */
@media screen and (max-width: 480px) {
  .home-header {
    padding: 10px 12px;
  }
  
  .circular-logo {
    width: 70px;
    height: 70px;
  }
  
  .logo-image {
    width: 50px;
    height: 50px;
  }
  
  .logo-text {
    font-size: 0.7rem;
    bottom: -18px;
  }
  
  .org-info {
    font-size: 0.7rem;
  }
  
  .welcome-title {
    font-size: 1.8rem;
    margin-bottom: 20px;
  }
  
  .home-main {
    padding: 20px 12px;
  }
  
  .checkin-button {
    font-size: 1.2rem;
    padding: 8px 18px;
  }
  
  .footer-content {
    gap: 8px;
  }
  
  .footer-text {
    font-size: 0.7rem;
  }
  
  .pits-logo {
    height: 25px;
  }
} 